@import 'theme.scss';

#bio {
  @include themify($themes) {
    color: themed('text-color'); } }

.publication-card {
    padding: 12px;
    margin-left: auto;
    margin-right: auto;
    font-size: 15px;

    .text-section {
        padding: 0px 10px 10px 10px; }

    .card-title {
        font-size: 18px;
        font-weight: 600;
        font-style: italic; }

    &__image {
        margin-top: 10px;
        // width: 90%
        max-height: 250px;
        max-width: 400px; }

    a {
        margin-right: 10px;
        text-decoration: underline;
        @include themify($themes) {
            text-decoration: none; } }

    p a {
        text-decoration: none; }

    @media (max-width: 800px) {
        flex-direction: column;
        align-items: center;

        &__image {
            width: 100%; } } }
